/* Core */
import { Component, NgZone, OnInit } from '@angular/core';
import { IonicPage, NavController, ModalController, NavParams } from 'ionic-angular';

/* Models */

/* Services */
import { LoaderService } from '../../app/services/loader.service';
import { ToastService } from '../../app/services/toast.service';
import { HighlightsService } from '../../app/services/highlights.service';
import { HighlightModel } from '../../app/models/highlight.model';
import { ClipsService } from '../../app/services/clips.service';
import { HighlightClipModel } from '../../app/models/highlightclip.model';
import { HighlightLinkModel } from '../../app/models/highlightlinks.model';

@IonicPage({
  defaultHistory: ['highlights'],
  name: 'highlightclips',
  segment: 'highlightclips/:highlight'
})
@Component({
  selector: 'page-highlightclips',
  templateUrl: 'highlightclips.html'
})
export class HighlightClipsPage implements OnInit {
  public highlightUuid: string;
  public highlight: HighlightModel;
  public isEditing = false;
  public highlightLinks: Array<HighlightLinkModel>;

  public clipsReady: Promise<any>;

  public removeList: any[] = [];

  constructor(
    public navCtrl: NavController,
    private ngZone: NgZone,
    private navParams: NavParams,
    public modalCtrl: ModalController,

    /* Services */
    private highlightsService: HighlightsService,
    private clipsService: ClipsService,
    private loaderService: LoaderService,
    private toastService: ToastService) {
      this.highlightUuid = this.navParams.get('highlight');
    }

  async ngOnInit(): Promise<any> {
    await this.getHighlight();
    await this.getClips();
    await this.getHighlightLinks();

    this.highlight.clips.sort(this.sort);
  }

  edit() {
    this.isEditing = true;
  }

  async generate() {
    this.highlightsService.generate(this.highlightUuid);
  }

  /**
   * 
   */
  async getHighlightLinks(): Promise<any> {
    const highlightLinks = await this.highlightsService.getHighlightLinks(this.highlightUuid);
    const that = this;

    that.ngZone.run(() => {
      that.highlightLinks = highlightLinks;
    });
  }

  async update() {
    return this.highlightsService.update(this.highlight);
  }

  async save() {
    let allPromise: Promise<any> = null;
    let deletePromise: Promise<any> = null;
    let updateClipOrdersPromise: Promise<any> = null;
    let updateHighlightPromise: Promise<any> = null;

    this.isEditing = false;

    deletePromise = this.deleteClips();
    updateClipOrdersPromise = this.updateClipOrders();
    updateHighlightPromise = this.update();

    allPromise = Promise.all([deletePromise, updateClipOrdersPromise, updateHighlightPromise]);

    this.loaderService.show(allPromise);
    this.toastService.onFailure(allPromise);
  }

  async getClips(): Promise<any> {
    const promiseList = [];
    this.highlight.clips.forEach(async (clip: any, index: number) => {
      const promise = this.clipsService.getBySlug(clip.slug);
      const c = await promise;
      clip.clipOrder = index;
      clip.description = c.description;
      clip.thumbnail = c.thumbnails.medium;
      clip.broadcaster = c.broadcaster;
      clip.title = c.title;
      clip.originalDuration = c.duration;
    });

    this.clipsReady = Promise.all(promiseList);

    return await this.clipsReady;
  }

  async getHighlight(): Promise<any> {
    let that = this;
    let promise;

    promise = this.highlightsService.getByUuid(this.highlightUuid).then(function(highlight: HighlightModel) {
      that.ngZone.run(() => {
        that.highlight = highlight;
      });
    });

    that.loaderService.show(promise);
    that.toastService.onFailure(promise);

    return await promise;
  }

  /**
   * Moves bottom locally until the user saves the modifications
   *
   * @param clip
   */
  async moveBottom(clip: any) {
    while (clip.clipOrder < this.highlight.clips.length - 1) {
      this.moveDown(clip);
    }
  }

  /**
   * Moves down locally until the user saves the modifications
   *
   * @param clip
   */
  async moveDown(clip: any) {
    const currentIndex = clip.clipOrder;
    const nextIndex = currentIndex + 1;
    const nextClip = this.findClipWithIndex(nextIndex);

    clip.clipOrder += 1;
    nextClip.clipOrder -= 1;

    this.highlight.clips.sort(this.sort);
  }

  /**
   * Moves top locally until the user saves the modifications
   *
   * @param clip
   */
  async moveTop(clip: any) {
    while (clip.clipOrder > 0) {
      this.moveUp(clip);
    }
  }

  /**
   * Moves up locally until the user saves the modifications
   *
   * @param clip
   */
  async moveUp(clip: any) {
    const currentIndex = clip.clipOrder;
    const lastIndex = currentIndex - 1;
    const lastClip = this.findClipWithIndex(lastIndex);

    clip.clipOrder -= 1;
    lastClip.clipOrder += 1;

    this.highlight.clips.sort(this.sort);
  }

  /**
   * Removes locally until the user saves the modifications
   *
   * @param clip
   */
  async remove(clip: any) {
    this.removeList.push(clip);

    const index = this.findClipIndex(clip);

    this.highlight.clips.forEach((c: any, i: number) => {
      if (index === i) {
        this.highlight.clips.splice(index, 1);
      } else if (i > index) {
        c.clipOrder -=1 ;
      }
    });
  }

  /**
   *
   */
  private async deleteClips(): Promise<any> {
    const promiseList = [];
    this.removeList.forEach(clip => {
      promiseList.push(this.highlightsService.removeClipHighlight(this.highlightUuid, clip.uuid));
    });

    await Promise.all(promiseList);
  }

  /**
   *
   */
  private async updateClipOrders(): Promise<any> {
    const promiseList = [];

    this.highlight.clips.forEach((clip: any) => {
      const highlightClip = new HighlightClipModel();

      highlightClip.clipOrder = clip.clipOrder;
      highlightClip.duration = clip.duration;
      highlightClip.slug = clip.slug;
      highlightClip.startTime = clip.startTime;
      highlightClip.uuid = clip.uuid;

      const promise = this.highlightsService.updateHighlightClip(this.highlightUuid, highlightClip);
      promiseList.push(promise);
    });

    await Promise.all(promiseList);
  }

  private findClipIndex(clip: any): number {
    let index = null;
    this.highlight.clips.forEach((c: any, i: number) => {
      if (c.uuid === clip.uuid) {
        index = i;
      }
    });

    return index;
  }

  private findClipWithIndex(index: number): any {
    let clip: any = null;
    this.highlight.clips.forEach((c: any) => {
      if (c.clipOrder === index) {
        clip = c;
      }
    });

    return clip;
  }

  private sort(a: any, b: any) {
    return a.clipOrder - b.clipOrder;
  }
}



